import React, { useState } from "react";
import styles from "./MarketUpdateRow.module.scss";
import Button from "../atoms/Button";
import { useTheme } from "../../context/ThemeContext";
import MiniChart from "../atoms/MiniChart";
import FavoriteButton from "../atoms/FavoriteButton";

interface MarketUpdateRowProps {
    rank: number;
    logo: string;
    name: string;
    symbol: string;
    price: number;
    change24h: number;
    marketCap: number;
    sparklineData: number[];
    isActive: boolean;
    onTradeClick: () => void;
    percent_change_1h : number;
    percent_change_24h : number;
    percent_change_7d : number;
    percent_change_30d : number;
    percent_change_60d  : number;
    percent_change_90d : number;
    onFavoriteToggle: (coinId: number) => void;
    isFavorite: boolean;
}

const MarketUpdateRow = ({
    rank,
    logo,
    name,
    symbol,
    price,
    change24h,
    marketCap,
    sparklineData,
    isActive,
    onTradeClick,
    percent_change_1h,
    percent_change_24h,
    percent_change_7d,
    percent_change_30d,
    percent_change_60d,
    percent_change_90d,
    onFavoriteToggle,
    isFavorite
}: MarketUpdateRowProps) => {
    const { theme } = useTheme();
    const percentageChanges = [
        percent_change_1h,
        percent_change_24h,
        percent_change_7d,
        percent_change_30d,
        percent_change_60d,
        percent_change_90d
    ];

    return (
        <div className={`${styles.row} ${theme === 'dark' ? styles.dark : ''}`}>
            <div className={styles.rank}>
                {rank}
                <FavoriteButton
                    coinId={rank}
                    onFavoriteToggle={onFavoriteToggle}
                    isFavorite={isFavorite}
                />
            </div>
            <div className={styles.coin}>
                <img src={logo} alt={name} className={styles.logo} />
                <div className={styles.nameSymbol}>
                    <div className={styles.name}>{name}</div>
                    <div className={styles.symbol}>{symbol}</div>
                </div>
            </div>
            <div className={styles.price}>${price.toFixed(2)}</div>
            <div className={`${styles.change} ${change24h < 0 ? styles.red : styles.green}`}>
                {change24h.toFixed(2)}%
            </div>
            <div className={styles.marketCap}>${marketCap.toLocaleString()}</div>
            <div className={styles.sparkline}>
                <MiniChart data={percentageChanges} />
            </div>
            <Button
                className={`${styles.tradeButton} ${theme === 'dark' ? styles.dark : styles.light} ${isActive ? styles.active : ''}`}
                onClick={onTradeClick}
            >
                Trade
            </Button>
        </div>
    );
};

export default MarketUpdateRow;
