import React, { useEffect, useState } from "react";
import { fetchCryptoData } from "../../services/services";
import MarketUpdateRow from "../molecules/MarketUpdateRow";
import MarketUpdateHeader from "../molecules/MarketUpdateHeader";
import FavoriteList from "../molecules/FavoriteList"; 

interface CombinedData {
    id: number;
    name: string;
    symbol: string;
    logo: string;
    price: number;
    market_cap: number;
    volume_change_24h: number;
    sparkline: number[];
    percent_change_1h: number;
    percent_change_24h: number;
    percent_change_7d: number;
    percent_change_30d: number;
    percent_change_60d: number;
    percent_change_90d: number;
}

const MarketUpdate = () => {
    const [cryptoData, setCryptoData] = useState<CombinedData[]>([]);
    const [loading, setLoading] = useState(true);
    const [activeCoinId, setActiveCoinId] = useState<number | null>(null);
    const [favorites, setFavorites] = useState<number[]>([]); // Favori coinlerinizi tutmak için state

    useEffect(() => {
        const load = async () => {
            try {
                const priceData = await fetchCryptoData('listings');
                const filteredPriceData = priceData.data.filter((crypto: any) =>
                    ["BTC", "ETH", "BNB", "USDT", "SOL", "XRP", "ADA", "AVAX"].includes(crypto.symbol)
                );

                const ids = filteredPriceData.map((c: any) => c.id.toString());
                const infoData = await fetchCryptoData('info', ids);

                const merged: CombinedData[] = filteredPriceData.map((crypto: any) => ({
                    id: crypto.id,
                    name: crypto.name,
                    symbol: crypto.symbol,
                    logo: infoData.data[crypto.id]?.logo || '',
                    price: crypto.quote.USD.price,
                    market_cap: crypto.quote.USD.market_cap,
                    volume_change_24h: crypto.quote.USD.volume_change_24h,
                    percent_change_1h: crypto.quote.USD.percent_change_1h,
                    percent_change_24h: crypto.quote.USD.percent_change_24h,
                    percent_change_7d: crypto.quote.USD.percent_change_7d,
                    percent_change_30d: crypto.quote.USD.percent_change_30d,
                    percent_change_60d: crypto.quote.USD.percent_change_60d,
                    percent_change_90d: crypto.quote.USD.percent_change_90d,
                    sparkline: generateRandomSparkline(),
                }));

                setCryptoData(merged);
            } catch (err) {
                console.error("Veri çekme hatası:", err);
            } finally {
                setLoading(false);
            }
        };

        load();
    }, []);

    // Favoriye ekleme/çıkarma fonksiyonu
    const toggleFavorite = (id: number) => {
        setFavorites((prevFavorites) =>
            prevFavorites.includes(id)
                ? prevFavorites.filter((favId) => favId !== id) // Favoriden çıkar
                : [...prevFavorites, id] // Favoriye ekle
        );
    };

    return (
        <div style={{ marginTop: "2rem" }}>
            {loading ? (
                <div>Yükleniyor...</div>
            ) : (
                <div>
                    <MarketUpdateHeader />
                    <FavoriteList favorites={favorites} /> {/* Favori listesi bileşeni */}
                    {cryptoData.map((coin, index) => (
                        <MarketUpdateRow
                            key={coin.id}
                            rank={index + 1}
                            logo={coin.logo}
                            name={coin.name}
                            symbol={coin.symbol}
                            price={coin.price}
                            change24h={coin.volume_change_24h}
                            marketCap={coin.market_cap}
                            sparklineData={coin.sparkline}
                            percent_change_1h={coin.percent_change_1h}
                            percent_change_24h={coin.percent_change_24h}
                            percent_change_7d={coin.percent_change_7d}
                            percent_change_30d={coin.percent_change_30d}
                            percent_change_60d={coin.percent_change_60d}
                            percent_change_90d={coin.percent_change_90d}
                            isActive={coin.id === activeCoinId}
                            onTradeClick={() => setActiveCoinId(coin.id)}
                            isFavorite={favorites.includes(coin.id)} // Yıldız durumu
                            onFavoriteClick={() => toggleFavorite(coin.id)} // Yıldız tıklama işlemi
                        />
                    ))}
                </div>
            )}
        </div>
    );
};

export default MarketUpdate;

function generateRandomSparkline() {
    return Array.from({ length: 7 }, () => Math.random() * 100);
}
