import React, { useState } from "react";

interface FavoriteButtonProps {
    coinId: number;
    onFavoriteToggle: (coinId: number) => void;
    isFavorite: boolean;
}

const FavoriteButton = ({ coinId, onFavoriteToggle, isFavorite }: FavoriteButtonProps) => {
    const handleClick = () => {
        onFavoriteToggle(coinId);
    };

    return (
        <button onClick={handleClick} style={{ background: "none", border: "none", cursor: "pointer" }}>
            <img
                src={isFavorite ? "/path/to/filled-star.png" : "/path/to/empty-star.png"}
                alt="Favorite"
                style={{ width: 24, height: 24 }}
            />
        </button>
    );
};

export default FavoriteButton;
