import React from "react";

interface FavoriteListProps {
    favorites: number[];
}

const FavoriteList = ({ favorites }: FavoriteListProps) => {
    return (
        <div>
            <h3>Favoriler</h3>
            <ul>
                {favorites.map((id) => (
                    <li key={id}>Coin {id}</li> // Burada coin bilgilerini gösterebilirsiniz.
                ))}
            </ul>
        </div>
    );
};

export default FavoriteList;
